import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';
import 'bootstrap/dist/css/bootstrap.min.css';

import Form from 'react-bootstrap/Form';
import Tostada from "./Toast"
import { Toast } from 'bootstrap';
const Contacto= () => {
  const form = useRef();
  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_6tkph46', 'template_derttml', form.current, 'i5JaqjiKDWMG2t4y5')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
  };

  return (
    <>
    <Form ref={form} onSubmit={sendEmail} >
    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
      <Form.Label>Nombre</Form.Label>
      <Form.Control type="text" name="user_name" placeholder="Nombre" required={true} />
    </Form.Group>
    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
      <Form.Label>Email</Form.Label>
      <Form.Control type="email" name="message" placeholder="Email" required={true} />
    </Form.Group>
    <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1"  >
      <Form.Label>Mensaje</Form.Label>
      <Form.Control as="textarea" name="message" placeholder='Mensaje' rows={8} required={true}/>
    </Form.Group>
    
<Tostada/>
  </Form>
  
  </>  
);
}


export default Contacto
