
import { BrowserRouter,Route,Routes} from "react-router-dom";
import Portafolio from "./Contenedores/Portafolio"
import Error404 from "./Contenedores/Error404"


import Preloader from "./Contenedores/Preloader";
import {  useState } from "react"
import './App.css';

 const App = () => {
 const [Loader, setLoader ] = useState(true)

    
    
    function preLoader(){
    setLoader(false)
    document.body.style.display="initial"
    }
    setTimeout(preLoader, 2000);


  return ( 

 <>

<BrowserRouter>
{ Loader ?
<div>
<Preloader/>

  </div>
  :
<div>

<Routes>
<Route exact path="/"  element={ <Portafolio/>} />
<Route path='*' element={<Error404/>} />

</Routes>
</div>
 }
 </BrowserRouter >

</>
  
 
)}

export default App;
