import Contacto from "./Contacto"
import Works from "./Works"
import Redes from "./Redes"
import { Avatar } from "@mui/material"
import $, { event }  from "jquery"
import "../Estilos/Portafolio.scss"
import "../Estilos/Works.css"

const Portafolio =()=>{
  
  class StickyNavigation {
	
    constructor() {
      this.currentId = null;
      this.currentTab = null;
      this.tabContainerHeight = 70;
      let self = this;
      $('.et-hero-tab').click(function() { 
        self.onTabClick(event, $(this)); 
      });
      $(window).scroll(() => { this.onScroll(); });
      $(window).resize(() => { this.onResize(); });
    }
    
    onTabClick(event, element) {
      event.preventDefault();
      let scrollTop = $(element.attr('href')).offset().top - this.tabContainerHeight + 1;
      $('html, body').animate({ scrollTop: scrollTop }, 600);
    }
    
    onScroll() {
      this.checkTabContainerPosition();
      this.findCurrentTabSelector();
    }
    
    onResize() {
      if(this.currentId) {
        this.setSliderCss();
      }
    }
    
    checkTabContainerPosition() {
      let offset = $('.et-hero-tabs').offset().top + $('.et-hero-tabs').height() - this.tabContainerHeight;
      if($(window).scrollTop() > offset) {
        $('.et-hero-tabs-container').addClass('et-hero-tabs-container--top');
      } 
      else {
        $('.et-hero-tabs-container').removeClass('et-hero-tabs-container--top');
      }
    }
    
    findCurrentTabSelector(element) {
      let newCurrentId;
      let newCurrentTab;
      let self = this;
      $('.et-hero-tab').each(function() {
        let id = $(this).attr('href');
        let offsetTop = $(id).offset().top - self.tabContainerHeight;
        let offsetBottom = $(id).offset().top + $(id).height() - self.tabContainerHeight;
        if($(window).scrollTop() > offsetTop && $(window).scrollTop() < offsetBottom) {
          newCurrentId = id;
          newCurrentTab = $(this);
        }
      });
      if(this.currentId !== newCurrentId || this.currentId === null) {
        this.currentId = newCurrentId;
        this.currentTab = newCurrentTab;
        this.setSliderCss();
      }
    }
    
    setSliderCss() {
      let width = 0;
      let left = 0;
      if(this.currentTab) {
        width = this.currentTab.css('width');
        left = this.currentTab.offset().left;
      }
      $('.et-hero-tab-slider').css('width', width);
      $('.et-hero-tab-slider').css('left', left);
    }
    
  }
  
  new StickyNavigation();
return(
<>
<section class="et-hero-tabs">
    <h1>MATIAS A. MINONI</h1>
    <h3>WEB DEVELOPER</h3>
    <Avatar  className="avatar-cara"
  src="https://i.ibb.co/RQwPG95/crop1.jpg"
  sx={{ width: 200, height: 200 }}
/>

    <div class="et-hero-tabs-container">
      
      <a class="et-hero-tab" href="#tab-Acerca_de">Acerca de</a>
      
      <a class="et-hero-tab" href="#tab-Formacion">Formación academica</a>
      <a class="et-hero-tab" href="#tab-Trabajos">Trabajos</a>
      <a class="et-hero-tab" href="#tab-Contacto">Contacto</a>
      <a class="et-hero-tab" href="#tab-Redes">Redes</a>
      <span class="et-hero-tab-slider"></span>
    </div>
  </section>

  <main class="et-main">
    <section class="et-slide" id="tab-Acerca_de">
      <h1>Acerca de mí</h1>
      <h3>Soy un desarrollador web junior con un año de experiencia creando proyectos. Tengo 22 años y estoy en busqueda de crecer en el mundo de la informatica, aprendiendo nuevos lenguajes y tecnologías. Debajo encontrarás mas acerca de mí.</h3>
    </section>
    <section class="et-slide" id="tab-Formacion">
      <h1>Formación Academica</h1>
      <br />
      <h2>
        <li> Bachiller en ciencias sociales, René Favaloro 2012-2018. Graduado </li>
        <br />
        <br />
       <li> Introduccion a bases de datos en visual basic, Centro de formación profesional de gestión privada Mariano Moreno 2018-2019.</li>
         
         <h3> Conceptos basicos de programación y uso de bases de datos. </h3>
        <br />
       <li> Desarrollo Web Full Stack, Coder House 2021-2022. Graduado.</li>
        <br/>
        <h3>
       Desarrollo web Frontend y Backend en lenguaje HTML5, CSS3, SASS, SCSS, PHP, JAVASCRIPT, REACT, JSX, NODEJS y plantillas como PUG o EJS. 
        
       Desarrollo de bases de datos en FIREBASE, MYSQL, SLQITE3, MONGODB, MONGOOSE.
       
        Maquetacion Web(Mockup).
     
        SEO, Google Analytics y otras herramientas de analítica.
        </h3>
      </h2>
    </section>

    <section class="et-slide" id="tab-Trabajos">
      <h1>Trabajos</h1>
      <br/>
      <Works/>
      <br/>
<h2>Otros</h2>
 <h3> 
  <li>
    La veneciana(Heladeria) <br />
    Empleando tareas como camarero, limpieza, heladero, barista, cajero y control de stock
     (2020-2021)
  </li>
  <li>
  Tea Connection(Restaurant) <br />
  Empleando tareas como camarero, barista, cajero y control de stock
   (2021-Actual)
  </li>
  </h3>
  

   
    </section>


    <section class="et-slide" id="tab-Contacto">
       <h1>Contacto</h1>
       <h3>Enviame un mail!</h3>
       < div className='contacto_form' >
      <Contacto />
      </div>
    </section>
    <section class="et-slide" id="tab-Redes">
      <h1>Redes</h1>
      <div className='redes'>
      <Redes/>
      </div>
    </section>
  </main>
    </>

)

}
export default Portafolio